export default {
  SET_NEWS(state, news) {
    state.news = news;
  },
  SET_ASK(state, ask) {
    state.ask = ask;
  },
  SET_JOBS(state, jobs) {
    state.jobs = jobs;
  },
  SET_USER(state, user) {
    state.user = user;
  },
  SET_ITEM(state, item) {
    state.item = item;
  },
  SET_LIST(state, list) {
    state.list = list;
  },
  addOneItem(state, todoItem) {
    const obj = { completed: false, item: todoItem};
    localStorage.setItem(todoItem, JSON.stringify(obj));  //객체를  문자열로 변환
    state.todoItems.push(obj);
  },
  removeOneItem(state, payload) {
    localStorage.removeItem(payload.todoItem.item);
    state.todoItems.splice(payload.index, 1)  // 새로운 배열 반환  ( 시작지점, 삭제 갯수, 추가할것)
  },
  toggleOneItem(state, payload) {
    // todoItem.completed = !todoItem.completed;  안티패턴 프롭스로 내린걸 다시 이벤트로 불러옴
    state.todoItems[payload.index].completed = !this.state.todoItems[payload.index].completed ;
    localStorage.removeItem(payload.todoItem.item); //삭제하고
    localStorage.setItem(payload.todoItem.item,JSON.stringify(payload.todoItem)); //다시 문자열로 바꿔서 등록
  },
  clearAllItems(state) {
    localStorage.clear();
    state.todoItems = [];
  }
}